@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap);
:root {
	--color-primary:#4C566A;
	--color-primary-variant: #3B4252;
	--color-on-primary: #ECEFF4;
	--color-secondary: #5E81AC;
	--color-secondary-variant: #5376a1;
	--color-on-secondary: #ffffff;
	--color-tertiary: #434C5E;
	--color-surface: #ECEFF4;
	--color-on-surface: var(--color-primary);
	--color-window-background: var(--color-primary);
	--color-toolbar: var(--color-primary-variant);

	--dimen-icon-size: 16px;
	--dimen-medium-padding: 16px;
	--dimen-small-padding: 8px;
	--dimen-tiny-padding: 4px;
	--dimen-surface-radius: 4px;
}

body {
	margin: 0;
	font-family: 'Rubik', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #4C566A;
	background-color: var(--color-window-background);
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.toolbar {
    padding: 16px;
    background-color: var(--color-toolbar);
    box-shadow: 2px 2px 4px var(--color-tertiary);
}

.toolbar .title {
    font-weight: 500;
    font-size: 1.6em;
    color: var(--color-on-primary);
}
.component-root {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100vw;
    height: 100vh;
}

.component-root button {
    padding: var(--dimen-tiny-padding);
    background-color: var(--color-secondary);
    border-radius: var(--dimen-surface-radius);
    border: none;
    outline: none;
    color: var(--color-on-secondary);
    font-size: 0.9em;
    font-weight: 500;
}

.component-root button:hover {
    cursor: pointer;
    background-color: var(--color-secondary-variant);
}

.component-root .container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    flex-basis: 100%;
    flex: 1 1;
    margin-left: var(--dimen-medium-padding);
    margin-right: var(--dimen-medium-padding);
}

.component-root .container form input, textarea {
    width: 80%;
    margin-top: var(--dimen-tiny-padding);
    margin-bottom: var(--dimen-tiny-padding);
    padding: var(--dimen-small-padding);
    font-family: 'Rubik';
    font-size: 1em;
    background-color: var(--color-surface);
    border-radius: 6px;
    border: 1px solid var(--color-secondary);
    outline: none;
}

.component-root .container form input:hover, textarea:hover {
    border: 1px solid var(--color-secondary-variant);
}

.component-root .container form button {
    width: 80%;
    margin-top: var(--dimen-medium-padding);
    padding: var(--dimen-small-padding);
}

.sauce-list {
    -webkit-padding-start: 0px;
            padding-inline-start: 0px;
}

.sauce-list .sauce-container {
    margin: var(--dimen-medium-padding);
    padding: var(--dimen-medium-padding);
    background-color: var(--color-tertiary);
    border-radius: var(--dimen-surface-radius);
    border: 1px solid var(--color-primary-variant);
    list-style: none;
    color: var(--color-on-primary);
}

.sauce-list .sauce-container button {
    margin-right: var(--dimen-medium-padding);
    margin-bottom: var(--dimen-small-padding);
}


